<template>
  <div class="template-container" v-loading="loading">
    <div class="template-container-title">创建人群-号码文件</div>

    <el-form ref="form" :model="form">
      <!--选择数据类型 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">1.选择数据类型</div>
        <div class="temp-item-main">
            <div>
                <el-radio-group v-model="form.user_id_type"  class="temp-radio-group">
                    <span class="temp-radio-item" :key="key" v-for="(item, key) in user_id_type_list" style="padding-right:20px;">
                        <el-tooltip class="item" effect="light" :content="item.desc" placement="bottom">
                            <el-radio-button :label="key" >{{item.name}}</el-radio-button>
                        </el-tooltip>
                    </span>
                </el-radio-group>

            </div>
        </div>
      </div>
      <!--选择数据类型 end -->
      <!--上传文件 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">2.上传文件</div>
        <div class="temp-item-main">
            <div class="upload-desc">
            <ul class="list-group">
                <li>• 请将号码写在*.txt（UTF-8编码）格式的文档中，一行一个号码。</li>
                <li>• 支持直接上传*.txt格式文件，也支持对单个/多个*.txt文档进行压缩成Zip格式上传。</li>
                <li>• 单个文件大小不超过1G。</li>
                <!-- <li>• 上传多个文件，将自动创建成多个人群。</li> -->
            </ul>
            </div>
            <div style="width:330px;padding-top:15px;">
                <el-upload
                class="upload-demo"
                ref="upload"
                action="/Admin/Marketing_CustomAudienceFiles/add"
                :http-request="myUpload"
                :on-change="changeFromFile"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                :auto-upload="false"
                :limit=1>
                <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button> -->
                </el-upload>
            </div>
        </div>
      </div>
      <!--上传文件 end -->
      <!--人群名称 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">3.人群名称</div>
        <div class="temp-item-main">
            
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" v-model="form.name" @input="audienceNameChange" type="text" maxlength="32" />
              <span class="temp-item-inlayremark">{{ name_size }}/32</span>
            </div>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群描述：</label>
            <div class="temp-item-inlineblock">
                <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="（选填）" v-model="form.description"></el-input>
            </div>
          </div>
        </div>
      </div>
      <!--人群名称 end -->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">开始上传</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    this.getEnumList()
    // 获取session
    var arr,
      reg = new RegExp('(^| )PHPSESSID=([^;]*)(;|$)')
    if ((arr = document.cookie.match(reg))) {
      this.uploadHeaders = {
        'VUE-SESSID': unescape(arr[2])
      }
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      loading: false,
      name_size : 0,
      description_size : 0,
      form: {
        user_id_type: '',
        name: '',
        description: '',
      },
      file_name: '',
      user_id_type_list: {},
      fileList: [],
      upLoadData: {}
    }
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/audience')
    },
    audienceNameChange(e) {
      this.name_size = this.form.name.length
    },
    changeFromFile(file, fileList){
        this.file_name = file.name
    },
    myUpload(params){
        let file = params.file
        var that = this,
        url = '/Admin/Marketing_CustomAudienceFiles/add'
        that.loading = true
        var fd = new FormData()
        fd.append('account_id', that.account_id)
        fd.append('user_id_type', that.form.user_id_type)
        fd.append('name', that.form.name)
        fd.append('description', that.form.description)
        fd.append('type', that.form.type)
        fd.append('file', file)
        
        var xhr = new XMLHttpRequest()
        xhr.open('POST', url) //修改成自己的接口
        xhr.setRequestHeader('VUE-SESSID', this.uploadHeaders['VUE-SESSID'])
        xhr.send(fd)
        
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
            if (xhr.status == 200) {
                var respData = JSON.parse(xhr.response)
                that.loading = false
                if (respData.code == 0) {
                    that.$message({
                    type: 'success',
                    message: respData.msg
                    })
                    setTimeout(function() {
                        that.$router.push('/setting/audience')
                    }, 500)
                }else{
                    that.$message.error(respData.msg)
                }
            } else {
                that.loading = false
                that.$message('网络异常，请稍后再试！')
            }
            }
        }
    },
    onSubmit() {
        if(this.form.user_id_type == ''){
            this.$message.error('请选择数据类型')
            return false
        }
        if(this.file_name == ''){
            this.$message.error('请上传文件')
            return false
        }
        if(this.form.name == ''){
            this.$message.error('请填写人群名称')
            return false
        }
        this.$refs.upload.submit()
    },
    getEnumList() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.user_id_type_list = respData.data['USER_ID_TYPE']
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    handleRemove(file, fileList) {
        this.file_name = ''
    },
    handlePreview(file) {
        console.log(file);
    },
    

  },
}
</script>
<style scoped>
.upload-desc li {
    line-height: 1;
    font-size: 12px;
    margin-bottom: 10px;
    color: #666d72;
}
</style>



